* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;

  
}

.country-list{
  list-style: none;
  font-size: 15px;
}

.country-info{
  padding-left: 40px;
}

li{
  display: flex;
  margin-bottom: 5px;
}

img{
  margin-right: 10px;
}

span{
  font-size: 25px;
}